import { createStore } from 'solid-js/store'

const Cell = (num = 0) => {
  let input!: HTMLInputElement
  return <input value={num} ref={input} class="cell" />
}

export const SudokuGrid = () => {
  const size = 9
  const row = Array.from(Array(size)).fill(0)
  const [data, setData] = createStore<number[][]>(row.map(() => row.slice()))

  return (
    <>
      <div>hey</div>
      <div style={{ margin: '10px 50px' }}>
        {data.map((arr) => (
          <div>{arr.map((num) => Cell(num))}</div>
        ))}
      </div>
    </>
  )
}
